<template>
  <div>
    <!-- 弹出层的头部区域 -->
    <van-nav-bar title="频道管理">
      <template #right>
        <van-icon @click="closePannel" name="cross" size="0.37333334rem" color="white"/>
      </template>
    </van-nav-bar>
    <!-- 我的频道 -->
    <div class="my-channel-box">
      <div class="channel-title">
        <span>我的频道
          <span class="small-title">
            点击  {{ isEdit ? '删除' : '进入' }}  频道
          </span>
        </span>
        <span @click="isEdit = !isEdit">  {{ isEdit ? '完成' : '编辑' }}  </span>
      </div>
      <!-- 我的频道列表 -->
      <van-row type="flex">
        <van-col span="6" v-for="(selected, index) in selectedList" :key="index" @click="deleteSelectedChannel(selected)">
          <div class="channel-item van-hairline--surround"> {{selected.name}}
            <!-- 删除的徽标 -->
            <van-badge color="transparent" class="cross-badge" v-show="isEdit && selected.id !== 0">
              <template #content>
                <van-icon
                  name="cross"
                  class="badge-icon"
                  color="#cfcfcf"
                  size="0.32rem"
                />
              </template>
            </van-badge>
          </div>
        </van-col>
      </van-row>
    </div>

    <!-- 更多频道 -->
    <div class="more-channel-box">
      <div class="channel-title">
        <span>点击添加更多频道：</span>
      </div>
      <!-- 更多频道列表 -->
      <van-row type="flex">
        <van-col span="6" v-for="(unselected, index) in unselectedList" :key="index" @click="addUnselectedChannel(unselected)">
          <div class="channel-item van-hairline--surround">{{unselected.name}}</div>
        </van-col>
      </van-row>
    </div>
  </div>
</template>

<script>
  export default {
    props: {
      selectedList: Array,        // 用户已选频道列表
      unselectedList: Array   // 用户未选频道列表
    },
    data () {
      return {
        isEdit: false // 控制编辑状态
      }
    },
    methods: {
      //  添加频道
      addUnselectedChannel(unselected) {
        //  处于编辑状态
        if (this.isEdit === true) {
          this.$emit('add-channel', unselected)
        }
      },
      //  删除频道
      deleteSelectedChannel(selected) {
        if (this.isEdit === true) {
          if (selected.id !== 0) this.$emit('delete-channel', selected)
        } else {
          // 切换频道
          this.$emit('close-channel') // 关闭弹出层
          this.$emit('input', selected.id)
        }
      },
      //  关闭频道管理弹出层
      closePannel() {
        this.$emit('close-channel')
        // this.isEdit = false
      }
    }
  }
</script>

<style scoped lang="less">

  .van-nav-bar {
    background-color: #1edada;
    color: white;
  }

  .van-popup,
  .popup-container {
    background-color: transparent;
    height: 100%;
    width: 100%;
  }

  .popup-container {
    display: flex;
    flex-direction: column;
  }

  .pop-header {
    height: 90px;
    background-color: #007bff;
    color: white;
    text-align: center;
    font-size: 14px;
    position: relative;
    .title {
      width: 100%;
      position: absolute;
      bottom: 15px;
    }
  }

  .pop-body {
    flex: 1;
    overflow: scroll;
    padding: 8px;
    background-color: white;
  }

  .my-channel-box,
  .more-channel-box {
    .channel-title {
      display: flex;
      justify-content: space-between;
      font-size: 14px;
      line-height: 28px;
      padding: 0 6px;
    }
  }

  .channel-item {
    font-size: 12px;
    text-align: center;
    line-height: 36px;
    background-color: #fafafa;
    margin: 5px;
  }

  /*删除的微标 */
  .cross-badge {
    position: absolute;
    right: -3px;
    top: 0;
    border: none;
  }

  /*提示文字 */
  .small-title {
    font-size: 10px;
    color: gray;
  }
</style>
